.about {
  display: flex;
  background-color: #fff;
  padding: 80px 104px;
  max-width: calc(100% - 2 * 104px);
}

.about__img {
  object-fit: cover;
  border-radius: 50%;
  width: 100%;
  height: auto;
  align-self: center;
}

.about__img-container {
  max-width: 464px;
  display: flex;
}

.about__text-container {
  margin-left: 56px;
}

.about__title {
  font-family: "Roboto Slab", Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-size: 40px;
  color: #1a1b22;
}

@media screen and (max-width: 768px) {
  .about {
    padding: 61px 40px;
    max-width: calc(100% - 2 * 40px);
  }

  .about__img-container {
    max-width: 232px;
  }

  .about__text-container {
    margin-left: 32px;
  }

  .about__title {
    font-size: 30px;
    line-height: 1.3;
  }
}

@media screen and (max-width: 480px) {
  .about {
    padding: 32px 16px;
    max-width: calc(100% - 2 * 16px);
    flex-direction: column;
  }
  
  .about__img-container {
    max-width: 100%;
    justify-content: center;
  }

  .about__text-container {
    margin-left: 0;
  }
}
